@import "./../../tabs-block.scss";
@import "./../../banner.scss";
@import "./../../statistics.scss";
@import "./../../info-page.scss";

.payment-and-delivery {
  .statistic-info {
    background-color: #fbfbfb;
  }

  .page-banner_big {
    .banner-container {
      justify-content: flex-start;
      @media (max-width: $tablet) {
        justify-content: center;
      }
      &__title {
        margin-top: 240px;
        margin-right: 17px;
        @include rale-800-45;
        line-height: 55px;
        @media (max-width: $tablet) {
          margin-top: 0px;
        }
      }
      &__description {
        margin-top: 25px;
        @include rale-500-24-i;
        max-width: 700px;
        line-height: 30px;
        @media (max-width: $tablet) {
          margin-top: 10px;
        }
      }
      &__icon-play {
        margin-top: 68px;
        padding-left: 0;
        @media (max-width: $tablet) {
          margin-top: 20px;
        }
      }
    }
  }

  .tabs-block {
    padding-top: 70px;
    padding-bottom: 73px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
      @media (max-width: $mobile) {
        padding-top: 30px;
        padding-bottom: 30px;
      }
    }
    hr {
        border-color: rgba($color: #000000, $alpha: .08);
    }
    &__container {
      .tab-content-block {
        
        @media (max-width: 800px) {
          padding-top: 20px;
          @media (max-width: $mobile) {
            padding-top: 10px;
          }
        }
        .tabs-block__text-block-with-img {
            flex-direction: row;
          justify-content: space-between;
          @media (max-width: 750px) {
            flex-direction: column-reverse;
          }
        }
        .tabs-block__text-block {
            min-width: 330px;
            @media (max-width: 750px) {
                min-width: 0;
            }
          .tabs-block__title {
            margin-top: 6px;
            @include rale-800-18;
          }
          .tabs-block__text-blocks {
          }
          .tabs-block-icon-text-block {
            &__title {
              @include rale-600-16-i;
              display: flex;
              align-items: center;
              gap: 12px;
            }
            &__text {
              display: block;
              margin-left: 2px;
              margin-top: 11px;
              max-width: 410px;
              @include rale-500-16;
              line-height: 24px;
              letter-spacing: 0.01em;
            }
            .icon-bank {
            }
            .icon-wallet-money {
            }
            .icon-bank-card {
            }
            br {
            }
          }
        }
        .tabs-block__img-block {
          img {
          }
        }
        .tabs-block__text-block {
          .tabs-block__title {
          }
          .tabs-block__text-blocks {
          }
          .tabs-block-icon-text-block {
            &__title {
            }
            &__text {
            }
            .icon-bank,
            .icon-bank-card,
            .icon-wallet-money {
              position: relative;
              width: 48px;
              height: 48px;
              display: flex;
              justify-content: center;
              align-items: center;
              &::after {
                content: "";
                position: absolute;
                width: 48px;
                height: 48px;
                border-radius: 50%;
                background-color: rgba($color: #93b474, $alpha: 0.15);
                top: 0;
                left: 0;
              }
              &::before {
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 36px;
                height: 36px;
                border-radius: 50%;
                background-color: $darkGreen;
                color: $white;
              }
            }
            br {
            }
          }
        }
        .tabs-block__img-block {
          img {
          }
        }
        .tabs-block__text-block {
          .tabs-block__title {
          }
          .tabs-block__text-blocks {
            margin-top: 19px;
            display: flex;
            flex-direction: column;
            gap: 37px;
          }
          .tabs-block-icon-text-block {
            &__title {
            }
            &__text {
            }
            .icon-bank {
            }
            .icon-wallet-money {
            }
            .icon-bank-card {
            }
            br {
            }
          }
        }
        .tabs-block__img-block {
          img {
          }
        }
      }
      .tab-bar {
        .tab-bar-item.tab-button {
          @media (max-width: 500px) {
            flex-direction: column;
            justify-content: end;
            gap: 5px;
            font-size: 15px;
          }
        }
        .icon-package {
          font-size: 36px;
          color: #c4c4c4;
          @media (max-width: 500px) {
            font-size: 30px;
          }
        }
        .icon-wallet-card {
          font-size: 37px;
          color: #c4c4c4;
          @media (max-width: 500px) {
            font-size: 30px;
          }
        }
        .icon-lorry {
          font-size: 45px;
          color: #c4c4c4;
          @media (max-width: 500px) {
            font-size: 30px;
          }
        }
      }
    }
  }
}
